<template>
  <div class="addType">
    <h4 class="addTypeTitle">添加商品类型</h4>
    <form class="form-horizontal goodstypeForm">
      <div class="form-group">
        <span v-if="addSuccess" class="col-sm-offset-3 col-sm-2 addSuccess">{{stateText}}</span>
      </div>
      <div class="form-group">
        <span v-if="addFaild" class="col-sm-offset-3 col-sm-2 addFaild">{{stateText}}</span>
      </div>
      <div class="form-group">
        <label for="goodstype" class="col-sm-2 control-label">类型名称</label>
        <div class="col-sm-5">
          <input type="text" v-model="typename" class="form-control" id="goodstype" placeholder="商品类型名称">
        </div>
        <p v-if="warn===1" class="warnText">* {{warnText}}</p>
      </div>
      <div class="form-group">
        <label for="goodstypeintrol" class="col-sm-2 control-label">类型介绍</label>
        <div class="col-sm-5">
          <input type="text" v-model="typeintrol" class="form-control" id="goodstypeintrol" placeholder="类型介绍">
        </div>
        <p v-if="warn===2" class="warnText">* {{warnText}}</p>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-5">
          <button type="button" @click="addGoodsType" class="btn btn-default">提交</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'addType',
  data () {
    return {
      typename: '',
      typeintrol: '',
      warnText: '',
      warn: 0,
      addSuccess: false,
      addFaild: false,
      stateText: ''
    }
  },
  methods: {
    addGoodsType: function () {
      if (this.typename === '' || this.typename === null) {
        this.warnText = '商品类型名不能为空！'
        this.warn = 1
        return
      } else if (this.typeintrol === '' || this.typeintrol === null) {
        this.warnText = '商品类型介绍不能为空！'
        this.warn = 2
        return
      } else {
        this.$http.post('http://127.0.0.1:3000/admin/addGoodsType', {'name': this.typename, 'intro': this.typeintrol, 'date': new Date()}).then((success) => {
          this.stateText = '添加成功'
          this.addSuccess = true
          let _this = this
          setTimeout(function () {
            _this.addSuccess = false
          }, 1000)
          this.typename = ''
          this.typeintrol = ''
        }, (err) => {
          let _this = this
          this.stateText = '添加失败'
          this.addFaild = true
          setTimeout(function () {
            _this.addFaild = false
          }, 1000)
          console.log(err)
        })
      }
    }
  }
}
</script>

<style scoped>
.goodstypeForm{
  margin-top: 30px;
}
.addTypeTitle{
  font-size: 20px;
  margin: 20px 20px;
}
.warnText{
  color: red;
}
.addSuccess{
  color: green;
  font-size: 20px;
  font-weight: bolder;
}
.addFaild{
  color: red;
  font-size: 20px;
  font-weight: bolder;
}
</style>



