<template>
<div class="editeGoods">
    <h4 class="addGoodsTitle">修改商品</h4>
    <form class="form-horizontal goodsForm" enctype="multipart/form-data">
      <div class="form-group">
        <span v-if="addSuccess" class="col-sm-offset-3 col-sm-2 addSuccess">{{warnText}}</span>
      </div>
      <div class="form-group">
        <label for="goodsname" class="col-sm-2 control-label">商品名称</label>
        <div class="col-sm-5">
          <input type="text" v-model="goodsname" class="form-control" id="goodsname" placeholder="商品名称">
        </div>
        <p v-if="warn===1" class="warnText">* {{warnText}}</p>
      </div>
      <div class="form-group">
        <label for="price" class="col-sm-2 control-label">商品价格</label>
        <div class="col-sm-5">
          <input type="number" v-model="price" class="form-control" id="price" placeholder="商品价格">
        </div>
        <p v-if="warn===2" class="warnText">* {{warnText}}</p>
      </div>
      <div class="form-group">
        <label for="goodstype" class="col-sm-2 control-label">商品类型</label>
        <div class="col-sm-2">
          <select class="form-control" id="goodstype" v-model="selectedType">
            <option v-for="goodsType in allGoodsType" :value="goodsType.name">{{goodsType.name}}</option>
          </select>
        </div>
        <p v-if="warn===3" class="warnText">* {{warnText}}</p>
      </div>
      <div class="form-group">
        <label for="img" class="col-sm-2 control-label">商品图片</label>
        <div class="col-sm-5">
          <input type="file" v-on:change="imgChange" class="form-control" id="img">
        </div>
<!--         <p v-if="warn===2" class="warnText">* {{warnText}}</p> -->
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-5">
          <a type="button" @click="addGoods" class="btn btn-default">确认修改</a>
        </div>
      </div>
    </form>
  </div>
</template>

<script>

export default {
  beforeCreate () {
    this.id = this.$route.query.id
    console.log(this.id)
    this.$http.get('http://127.0.0.1:3000/admin/getGoodsInfo/?id=' + this.id).then(success => {
      this.allGoodsType = success.body.allGoodsType
      this.selectedType = success.body.goodsInfo.selectedType
      this.goodsname = success.body.goodsInfo.name
      this.price = success.body.goodsInfo.price
      this.oldgoodsType = success.body.goodsInfo.type.name
    }, err => {
      console.log(err)
    })
  },
  name: 'editeGoods',
  data () {
    return {
      warn: 0,
      warnText: '',
      allGoodsType: [],
      selectedType: '',
      oldgoodsType: '',
      id: '',
      goodsname: '',
      price: '',
      formdata: new FormData(),
      addSuccess: false
    }
  },
  methods: {
    addGoods: function () {
      if (this.goodsname === '' || this.goodsname === null) {
        this.warnText = '商品名不能为空！'
        this.warn = 1
        return
      } else if (this.price === '' || this.price === null) {
        this.warnText = '价格不能为空！'
        this.warn = 2
        return
      } else if (this.selectedType === '' || this.selectedType === null) {
        this.warnText = '类型不能为空！'
        this.warn = 3
        return
      } else {
        this.id = this.$route.query.id
        this.formdata.append('id', this.id)
        this.formdata.append('price', this.price)
        this.formdata.append('name', this.goodsname)
        this.formdata.append('selectedType', this.selectedType)
        this.$http.post('http://127.0.0.1:3000/admin/editeGoods', this.formdata).then(success => {
          if (success.body.result === 1) {
            this.addSuccess = true
            this.warnText = '修改成功'
            let _this = this
            setTimeout(function () {
              _this.addSuccess = false
            }, 1000)
            this.price = ''
            this.goodsname = ''
            this.selectedType = ''
            this.formdata = new FormData()
            this.$router.go(-1)
            return
          }
        }, err => {
          let _this = this
          this.stateText = '修改失败'
          this.addFaild = true
          setTimeout(function () {
            _this.addFaild = false
          }, 1000)
          console.log(err)
        })
      }
    },
    imgChange: function (e) {
      var files = e.target.files || e.dataTransfer.files
      if (!files.length) return
      this.formdata.append('img', files[0])
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.goodsForm{
  margin-top: 30px;
}
.warnText{
  color: red;
}
.addGoodsTitle{
  font-size: 20px;
  margin: 20px 20px;
}
.addSuccess{
  color: green;
  font-size: 20px;
  font-weight: bolder;
}
</style>




