<template>
  <div id="left">
      <ul>
       <li>
       		<li class="oneMenu"  v-on:click="one=!one">订单信息管理</li>
       		<transition name="fade">
       			<ul v-if="one" class="twoMenuContent">
	       			 <router-link to="/neworder" tag="li" active-class="active">未处理订单</router-link>
	       				<router-link to="/oldorder" tag="li" active-class="active">已处理订单</router-link>
       			</ul>
       		</transition>
       </li>
       <li>
       		<li class="oneMenu" v-on:click="two=!two">菜单管理</li>
       		<transition name="fade">
       			<ul v-if="two" class="twoMenuContent">
	       			<router-link to="/allGoods" tag="li" active-class="active">已有商品</router-link>
	       			<router-link to="/addGoods" tag="li" active-class="active">添加商品</router-link>
              <router-link to="/addType" tag="li" active-class="active">添加商品类型</router-link>
       			</ul>
       		</transition>
       </li>
       <li>
       		<li class="oneMenu" v-on:click="three=!three">用户管理</li>
       		<transition name="fade">
       			<ul v-if="three" class="twoMenuContent">
	       			<router-link to="/signuser" tag="li" active-class="active">注册用户</router-link>
       			</ul>
       		</transition>
       </li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'left',
  data () {
    return {
      one: false,
      two: false,
      three: false
    }
  }
}
</script>

<style>
.active:hover{
		background-color: #1e89e0;
}
.active{
		background-color: #1e89e0;
}
li:hover{
		background-color: #000;
		cursor: pointer;
}
#left{
		display: inline-block;
    float: left;
    top: 60px;
    left: 0;
	  color:#fff;
		width: 200px;
		height: 695px;
		background-color: #202020;
}
.oneMenu{
		padding: 15px 5px;
		border-bottom: 0.1px solid #000;
}
.twoMenuContent li{
		padding: 12px 20px;
		border-bottom: 0.1px solid #000;
}
@media screen and (max-width:1200px){
 #left{
  display: none;
 }   
}
</style>



