<template>
  <div class="neworder">
  	<h4 class="allGoodsList">所有商品列表</h4>
    <div class="roomInfo">
	    <div class="room">
	      <table class="roomTable table">
	        <tr class="row goodsHeader">
	          <th class="col-sm-2">商品名</th>
	          <th class="col-sm-1">价格</th>
	          <th class="col-sm-1">销量</th>
	          <th class="col-sm-2">类型</th>
	          <th class="col-sm-2">缩略图</th>
	          <th class="col-sm-2">操作</th>
	        </tr>
	        <tr class="row" v-for="goods in allgoodsData">
		        <td class="col-sm-2">{{goods.name}}</td>
		          <td class="col-sm-1">{{goods.price}}/元</td>
		          <td class="col-sm-1">{{goods.sales}}</td>
		          <td class="col-sm-2">{{goods.type[0].name}}</td>
		          <td class="col-sm-2"><img :src=goods.img></td>
		          <td class="col-sm-2">
		          	<router-link :to="{ path: '/editeGoods', query: { id: goods._id }}" tag="a" class="edite">修改</router-link>
		          	<a href="javascript:void(0)" @click="cancelGoods(goods._id)" class="edite">下架</a>
		          </td>
		        </tr>
	      </table>
	    </div>
	    <nav aria-label="Page navigation" class="fenye">
	      <ul class="pagination">
	        <li v-if="this.currPage >0">
	          <a href="javascript:void(0)" aria-label="Previous" @click="fetchData(-1)">
	            <span aria-hidden="true">上一页</span>
	          </a>
	        </li>
	        <li>
	            <span aria-hidden="true">{{(this.currPage+1)}}/{{allPage}}</span>
	        </li>
	        <li v-if="this.currPage < this.allPage-1">
	          <a href="javascript:void(0)" aria-label="Next" @click="fetchData(+1)">
	            <span aria-hidden="true">下一页</span>
	          </a>
	        </li>
	        <li>
	            <span aria-hidden="true">总共{{this.count}}条数据</span>
	        </li>
	      </ul>
	    </nav>
	   </div>
	   <div class="modalContent" v-if="showContent">
	   </div>
	   <div class="myModal" v-if="showContent">
	   			<div>确定下架此商品?</div>
	   			<a href="javascript:void(0)" @click="noCancel" class="cancelbtn btn btn-info">取消</a>
	   			<a href="javascript:void(0)" @click="sureCancel" class="surebtn btn btn-warning">确认</a>
	   	</div>
  </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$http.get('http://127.0.0.1:3000/admin/getAllGoods/?page=0').then((success) => {
      this.allgoodsData = success.body.result
      this.count = success.body.count
      this.currPage = success.body.currPage
      this.allPage = success.body.allPage
    }, (err) => {
      console.log(err)
    })
  },
  name: 'allGoods',
  data () {
    return {
      allgoodsData: [],
      count: 0,
      currPage: 0,
      allPage: 0,
      showContent: false,
      cancelgoodsId: ''
    }
  },
  methods: {
    fetchData: function (page) {
      this.$http.get('http://127.0.0.1:3000/admin/getAllGoods/?page=' + (this.currPage + page)).then((success) => {
        this.allgoodsData = success.body.result
        this.count = success.body.count
        this.currPage = success.body.currPage
        this.allPage = success.body.allPage
      }, (err) => {
        console.log(err)
      })
    },
    cancelGoods: function (id) {
      this.showContent = true
      this.cancelgoodsId = id
    },
    noCancel: function () {
      this.showContent = false
    },
    sureCancel: function () {
      this.$http.get('http://127.0.0.1:3000/admin/cancelGoods/?id=' + this.cancelgoodsId).then((success) => {
        this.$router.go(0)
      }, (err) => {
        console.log(err)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.roomTable{
		margin-left: 40px;
}
.roomTable th,.roomTable td{
	text-align: center;
}
.roomTable td,.roomTable th{
	border: 1px solid #ccc;
	padding: 5px 5px;
}
.roomTable th{
	line-height: 30px;
	height: 30px;
}
.roomTable td{
	line-height: 100px;
	height: 100px;
}
.allGoodsList{
	font-size: 20px;
 margin: 20px 0px;
}
tr th{
	background-color: #1e89e0;
	color: #fff;
}
.edite{
	color: blue;
}
.fenye{
	float: right;
	margin-right: 170px;
}
.modalContent{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #000;
		opacity: 0.2;
}
.myModal{
	 width: 250px;
	 height: 120px;
/*	 border:1px solid red;*/
	 position: fixed;
	 margin: auto;
	 left: 0;
	 right: 0;
	 top: 0;
	 bottom: 0;
	 z-index: 9999999;
	 background-color: #fff;
}
.cancelbtn{
		float: right;
		margin-top: 50px;
		margin-right: 20px;
		margin-left: 20px;

}
.surebtn{
	float: right;
	margin-top: 50px;
}
.myModal div{
		padding: 5px;

}
</style>



