import Vue from 'vue'
import Router from 'vue-router'
import NewOrder from '@/components/newOrder'
import AddGoods from '@/components/addGoods'
import AddType from '@/components/addType'
import AllGoods from '@/components/allGoods'
import VueResource from 'vue-resource'
import EditeGoods from '@/components/editeGoods'
import Signuser from '@/components/signuser.vue'
import OldOrder from '@/components/oldOrders.vue'
Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  routes: [
    {
      path: '/neworder',
      name: 'newOrder',
      component: NewOrder
    },
    {
      path: '/oldorder',
      name: 'oldOrder',
      component: OldOrder
    },
    {
      path: '/addGoods',
      name: 'addGoods',
      component: AddGoods
    },
    {
      path: '/addType',
      name: 'addType',
      component: AddType
    },
    {
      path: '/allGoods',
      name: 'allGoods',
      component: AllGoods
    },
    {
      path: '/editeGoods',
      name: 'editeGoods',
      component: EditeGoods
    },
    {
      path: '/signuser',
      name: 'signuser',
      component: Signuser
    }
  ]
})



