<template>
  <div id="header">
      <div class="title"><router-link to="/" tag="a" class="menuLine">优优</router-link></div>
      <div class="admin"><a href="javascript:void(0)" @click="loginOut()">退出登录</a></div>
  </div>
</template>

<script>
export default {
  name: 'header',
  methods: {
    loginOut: function () {
      this.$http.get('http://127.0.0.1:3000/admin/loginOut').then((success) => {
        if (success.body.result === 1) {
          window.location = 'http://127.0.0.1:3000/admin'
        } else {
          return
        }
      }, (err) => {
        console.log(err)
      })
    }
  }
}
</script>

<style>

a,a:hover,a:active,a:visited,a:link{
	color: #fff;
	text-decoration: none;
}
#header{
  width: 100%;
  height: 60px;
  background-color: #1e89e0;
}
.title{
	 font-size: 25px;
	 display: inline-block;
	 float: left;
	 margin-left: 10%;
	 margin-top: 15px;
}
.admin{
		display: inline-block;
		float: right;
		margin-right: 200px;
		margin-top: 30px;
}
</style>



