<template>
  <div class="neworder">
  	<h4 class="signuser">所有未处理订单</h4>
  		<div class="allusers">
  			<table class="roomTable table">
	        <tr class="row usersHeader">
	          <th class="col-sm-1">手机号</th>
	          <th class="col-sm-2">商品名</th>
	          <th class="col-sm-4">地址</th>
	          <th class="col-sm-2">日期</th>
	          <th class="col-sm-1">状态</th>
	          <th class="col-sm-2">处理</th>
	        </tr>
	        <tr class="row" v-for="order in allordersData">
	          <td class="col-sm-1">{{order.user[0].phone}}</td>
	          <td class="col-sm-2">{{order.goods[0].name}}</td>
	          <td class="col-sm-4">{{order.user[0].address}}</td>
	          <td class="col-sm-2">{{formateDate(order.date)}}</td>
	          <td class="col-sm-1" style="color:red">未处理</td>
	          <td class="col-sm-2"><a href="javascript:void(0)" @click="doOrder(order._id)" class="delete">处理</a></td>
	        </tr>
	    </table>
	    <nav aria-label="Page navigation" class="fenye">
	      <ul class="pagination">
	        <li v-if="this.currPage >0">
	          <a href="javascript:void(0)" aria-label="Previous" @click="fetchData(-1)">
	            <span aria-hidden="true">上一页</span>
	          </a>
	        </li>
	        <li>
	            <span aria-hidden="true">{{(this.currPage+1)}}/{{allPage}}</span>
	        </li>
	        <li v-if="this.currPage < this.allPage-1">
	          <a href="javascript:void(0)" aria-label="Next" @click="fetchData(+1)">
	            <span aria-hidden="true">下一页</span>
	          </a>
	        </li>
	        <li>
	            <span aria-hidden="true">总共{{this.count}}条数据</span>
	        </li>
	      </ul>
	    </nav>
  		</div>
  </div>
</template>

<script>
export default {
  beforeCreate () {
    this.$http.get('http://127.0.0.1:3000/admin/getAllNewOrders/?page=0').then((success) => {
      console.log(success)
      this.allordersData = success.body.result
      this.count = success.body.count
      this.currPage = success.body.currPage
      this.allPage = success.body.allPage
    }, (err) => {
      console.log(err)
    })
  },
  data () {
    return {
      allordersData: [],
      count: 0,
      currPage: 0,
      allPage: 0
    }
  },
  methods: {
    fetchData: function (page) {
      this.$http.get('http://127.0.0.1:3000/admin/getAllNewOrders/?page=' + (this.currPage + page)).then((success) => {
        this.allordersData = success.body.result
        this.count = success.body.count
        this.currPage = success.body.currPage
        this.allPage = success.body.allPage
      }, (err) => {
        console.log(err)
      })
    },
    doOrder: function (id) {
      this.$http.get('http://127.0.0.1:3000/admin/doOrders/?id=' + id).then((success) => {
        if (success.body.result === 1) {
          this.$router.go(0)
        }
      }, (err) => {
        console.log(err)
      })
    },
    formateDate: function (date) {
      let adate = new Date(date)
      return adate.getFullYear() + '/' + (adate.getMonth() + 1) + '/' + adate.getDate()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
	color: blue;
}
.signuser{
	font-size: 20px;
 margin: 20px 0px;
}

.roomTable th,.roomTable td{
	text-align: center;
}
.roomTable td,.roomTable th{
	border: 1px solid #ccc;
	padding: 5px 5px;
}
.roomTable th{
	line-height: 30px;
	height: 30px;
}
.roomTable td{
	line-height: 100px;
	height: 100px;
}
.usersHeader{
	height: 40px;
	line-height: 40px;
}
tr th{
	background-color: #1e89e0;
	color: #fff;
}
.fenye{
	float: right;
	margin-right: 170px;
}
</style>



